import '@servicenow/now-collapse';
import '@servicenow/now-button';
import '@servicenow/now-loader';
import '@servicenow/now-empty-state';
import '@servicenow/now-highlighted-value';
import {t} from 'sn-translate';
import {ACTIONS, FACET_CONSTANTS} from '../constants';
import moment from 'moment';

const _renderFacetsHeading = (title = t('All activity')) => {
	return <div className="facet-group-container">
		<div className="padding-left facet-group-items">
			<span
				title={title}
				className="facet-title display facet-allow-ellipses">
				{title}
			</span>
		</div>
	</div>;
};

const _renderIcon = (icon) => {
	return <span className="facet-icon sm">
		<now-icon
			icon={icon}
			size="md">
		</now-icon>
	</span>;
};

const _showCount = (count) => {
	if (!count)
		return '';

	return <span className="facet-activities-count">
		({count})
	</span>;
};

const _renderMetadata = (metadata, dateFormat, timeZoneOffset) => {
	if (metadata && metadata.length > 0) {
		let primaryObj = {};

		//Hardcoded solution for Paris, as discussed.
		const buildLabelValueStackObject = (data) => {
			let obj;
			if (data.key == 'number') {
				obj = {
					'type': 'highlighted-value',
					'label': data.value,
					'status': 'low'
				};
				primaryObj = {...obj, ...primaryObj};
				return primaryObj;
			} else if (data.key == 'active_escalation' ||
				data.key == 'active_account_escalation') {
				if (data.value && data.value.length > 0) {
					primaryObj.status = 'critical';
				}
				return null;
			}
			if (data.type == 'glide_date_time') {
				return {
					'type': 'string',
					'value': data.raw_value ? moment.utc(data.raw_value).utcOffset(
						timeZoneOffset).format(dateFormat) : data.value
				};
			}
			return {
				'type': 'string',
				'value': data.value
			};
		};

		const stackObjects = metadata.map((data) => {
			if (!data)
				return data;
			return buildLabelValueStackObject(data);
		}).filter((data) => data != null).map((item) => {
			if (item.type == 'highlighted-value') {
				return <span className='metadata'>
					<now-highlighted-value
						label={item.label}
						status={item.status}
						show-icon={true}>
					</now-highlighted-value>
				</span>;

			} else
				return <span className='metadata' title={item.value}>
					{item.value}
				</span>;

		});

		return <div className='facet-metadata'>
			{stackObjects}
		</div>;
	}
	return '';
};

const _renderFacetChild = (
	{title, icon, activity_count, showCountOnFacet, is_dynamic, metadata}, dateFormat, timeZoneOffset
) => {
	return <div>
		<div className="facet-icon-container">
			{icon ? _renderIcon(icon) : ''}
			<span
				title={title}
				className={'facet-allow-ellipses facet-title ' + (showCountOnFacet ? 'with-counts' : '')}>{title}</span>
			{showCountOnFacet ? _showCount(activity_count) : ''}
		</div>
		{is_dynamic ? _renderMetadata(metadata, dateFormat, timeZoneOffset) : ''}
	</div>;
};

const _renderShowMoreFacets = (facet, dispatch) => {
	return <span className="facet-show-more">
		<now-button
			label={t('Show all')} tabindex="-1" size="sm" className='facet-show-more-button' variant="secondary"
			on-click={(event) => {
				event.stopPropagation();
				event.preventDefault();
				dispatch(ACTIONS.FACET_SHOW_MORE_REQUESTED, {facet});
			}}>
		</now-button>
	</span>;
};

const _renderFacetChildren = (
	facet, {
		expandedFacet, selectedFacet, timeZoneOffset,
		properties: {showCountOnFacet, dateFormat}
	}, {dispatch}, childFacetLimit) => {
	const {is_dynamic, facets, children, icon, key} = facet;
	const _children = children.slice(0, is_dynamic ? childFacetLimit : children.length);
	const childrenDOM = _children.map((child) => {
		const {title, sys_id, facet, key: _childFacetKey} = child;
		return <li>
			<div
				className={'facet-child padding-left facet-item ' +
				(selectedFacet == _childFacetKey ? 'facet-selected' : '') +
				(key == expandedFacet ? '' : 'facet-hidden')
				} tabindex="-1" role="button"
				on-click={(event) => {
					let payload = {
						'selectedFacet': _childFacetKey,
						sys_id,
						facet: facet ? facet : (facets[0] || null),
						is_dynamic,
						title,
						'key': _childFacetKey
					};
					dispatch(ACTIONS.FACET_SELECTED, payload);

					//Do not propagate the event to ancestors.
					event.stopPropagation();
					event.preventDefault();
				}}>
				{_renderFacetChild({icon, ...child, showCountOnFacet, is_dynamic}, dateFormat, timeZoneOffset)}
			</div>
		</li>;
	});

	return <now-collapse
		id={key} expanded={key == expandedFacet}
		className={key == expandedFacet ? 'expanded' : ''}>
		<ol>
			{childrenDOM}
			<li className='show-more-list-items'>
				{is_dynamic && (childFacetLimit < children.length) ? _renderShowMoreFacets(facet, dispatch) : ''}
			</li>
		</ol>
	</now-collapse>;
};

const _renderFacetGroupIcon = (facet, hasChildren, iState) => {
	const {key} = facet;
	const {expandedFacet, properties: {facetCollapseIcon, facetExpandIcon}} = iState;
	const icon = expandedFacet == key ? facetExpandIcon : facetCollapseIcon;

	return <span className="facet-group-icon">
		<now-icon icon={icon} size="md">
		</now-icon>
	</span>;
};

const _renderFacetGroup = (facet, hasChildren, iState) => {
	const {properties: {showCountOnFacet, loadChildFacetsOnDemand}} = iState;
	const {name, activity_count, is_dynamic} = facet;
	const showIcon = hasChildren || (is_dynamic && loadChildFacetsOnDemand);
	return <div className={(showIcon ? '' : 'padding-left ') + 'facet-group-container'}>
		{showIcon ? _renderFacetGroupIcon(facet, hasChildren, iState) : ''}
		<div className='facet-group-items'>
			<span className="facet-title display">
				<span
					title={name}
					className={'facet-allow-ellipses ' + (showCountOnFacet ? 'with-counts' : '')}>
					{name}
				</span>
				{showCountOnFacet ? _showCount(activity_count) : ''}
			</span>
		</div>
	</div>;
};

const _renderFacets = (state, helpers) => {
	const {facets, properties: {childFacetsListSize}} = state;

	const facetsDOM = facets.map((facet) => {
		const hasChildren = facet.children && facet.children.length > 0;
		const isSelected = facet.key == state.selectedFacet; //Level-1 facets
		const isExpanded = facet.key == state.expandedFacet;
		const childFacetLimit = Math.min(
			Math.max(childFacetsListSize, facet.childrenDisplayLimit || 0), facet.children.length);

		return <li>
			<div
				className={
					'facet-group facet-item ' +
					(isSelected ? 'facet-selected ' : '') +
					(isExpanded ? 'facet-expanded' : 'facet-collapsed')}
				tabindex="-1" role="button"
				aria-expanded={isExpanded ? 'true' : 'false'}
				on-click={(event) => {
					//Do not propagate the event to ancestors.
					event.stopPropagation();
					event.preventDefault();

					if (!isSelected)
						helpers.dispatch(ACTIONS.FACET_GROUP_SELECTED, {
							facet
						});
					else if (hasChildren) {
						helpers.updateState({
							path: 'expandedFacet',
							value: (isExpanded ? '' : facet.key),
							operation: 'set'
						});
					}
				}}>
				{_renderFacetGroup(facet, hasChildren, state, helpers)}
			</div>
			{hasChildren ? _renderFacetChildren(facet, state, helpers, childFacetLimit) : ''}
		</li>;
	});

	return <div className="facet">
		<ol className="facet-level-1">
			<li>
				<div
					role="button" tabindex="0"
					className={'facet-group facet-head facet-item' +
					(state.selectedFacet == FACET_CONSTANTS.ALL_FACETS_KEY ? ' facet-selected' : '')}
					on-click={(event) => {
						event.stopPropagation();
						event.preventDefault();
						helpers.dispatch(ACTIONS.FACET_GROUP_SELECTED,
							{
								facet: {
									name: FACET_CONSTANTS.ALL_ACTIVITY_HEAD,
									title: t('All activity'),
									key: FACET_CONSTANTS.ALL_FACETS_KEY // Assuming ALL_FACETS_KEY as FACET_CONSTANTS.ALL_ACTIVITY_HEAD
								}
							});
					}}>
					{_renderFacetsHeading()}
				</div>
			</li>
			{facetsDOM}
		</ol>
	</div>;
};

const _renderNoFacets = () => {
	return <div>
		<now-empty-state
			type="empty" variant="medium" icon={null}
			headerText="No facets found"
			bodyText="No facets configured for this feed">
		</now-empty-state>
	</div>;
};

const _renderFacetsLoading = () => {
	return <div className="facets-loading">
		<now-loader size="lg">
		</now-loader>
	</div>;
};

const view = (state, helpers) => {
	const {isFacetsLoading, facets, properties: {facets: facetProperty}} = state;
	if (isFacetsLoading && facets && facets.length == 0 && facetProperty
		&& Object.keys(facetProperty).length > 0)
		return _renderFacetsLoading();

	if (!facets || facets.length == 0)
		return _renderNoFacets();

	return _renderFacets(state, helpers);
};

export default view;
