import keyBindingBehavior from '@servicenow/behavior-key-binding';

const behaviors = [
	{
		behavior: keyBindingBehavior,
		options: {
			keyBindings: {
				moveBetweenRows: ['ArrowDown', 'ArrowUp', 'Home', 'End'],
				spaceEnterKey: [' ', 'Enter']
			},
			handlers: {
				spaceEnterKey: (host, event) => {
					if (!host || !host.shadowRoot || !host.shadowRoot.activeElement)
						return;

					if (!(host.shadowRoot.activeElement.classList.contains('facet-item')
						|| host.shadowRoot.activeElement.classList.contains('facet-show-more-button')))
						return;
					event.preventDefault();
					event.stopPropagation();
					const activeElm = host.shadowRoot.activeElement;
					if (activeElm)
						activeElm.click();
					if (host.shadowRoot.activeElement.classList.contains('facet-show-more-button')) {
						const elements = host.shadowRoot.querySelectorAll(
							'.facet-item:not(.facet-hidden),  now-collapse.expanded .facet-show-more now-button');
						const elementAt = Array.prototype.indexOf.call(elements, activeElm);

						setTimeout(() => {
							const elements = host.shadowRoot.querySelectorAll(
								'.facet-item:not(.facet-hidden),  now-collapse.expanded .facet-show-more now-button');
							elements[elementAt].focus();
						}, 100);
					}
				},
				moveBetweenRows: (host, event) => {
					if (!host || !host.shadowRoot || !host.shadowRoot.activeElement)
						return;

					if (!(host.shadowRoot.activeElement.classList.contains('facet-item')
						|| host.shadowRoot.activeElement.classList.contains('facet-show-more-button')))
						return;
					event.preventDefault();
					const elements = host.shadowRoot.querySelectorAll(
						'.facet-item:not(.facet-hidden),  now-collapse.expanded .facet-show-more now-button');
					const activeElm = host.shadowRoot.activeElement;
					const elementAt = Array.prototype.indexOf.call(elements, activeElm);
					let nextIndex;

					switch (event.key) {
					case 'ArrowDown':
						nextIndex = elementAt + 1;
						break;
					case 'ArrowUp':
						nextIndex = elementAt - 1;
						break;
					case 'Home':
						nextIndex = 0;
						break;
					case 'End':
						nextIndex = elements.length;
						break;
					}
					if (nextIndex >= elements.length)
						nextIndex = elements.length - 1;
					else if (nextIndex < 0)
						nextIndex = 0;

					elements[nextIndex].focus();
				},
			}
		}
	}
];

export default behaviors;
