import {ACTIONS} from "./constants";

export const dispatches = {
	/**
	 * Event dispatched when facets are requested.
	 *
	 * @type {{showCounts: boolean, lazyLoad: boolean, startDate: string, endDate: string, lastFeedRefreshedOn: string }}
	 */
	[ACTIONS.FACETS_REQUESTED]: {
		schema: {
			type: 'object',
			properties: {
				showCounts: { type: 'boolean' },
				lazyLoad: { type: 'boolean' },
				startDate: { type: 'string' },
				endDate: { type: 'string' },
				lastFeedRefreshedOn: { type: 'string' }
			}
		}
	},

	/**
	 * Event dispatched when feed requested.
	 *
	 * @type {{windowFrom: integer, windowTo: integer, facets: array, recordId: string, startDate: string, endDate: string, lastFeedRefreshedOn: string }}
	 */
	[ACTIONS.FEED_REQUESTED]:{
		schema: {
			type: 'object',
			properties: {
				windowFrom: { type: 'integer' },
				windowTo: { type: 'integer' },
				facets: { type: 'array' },
				recordId: { type: 'string' },
				startDate: { type: 'string' },
				endDate: { type: 'string' },
				lastFeedRefreshedOn: { type: 'string' }
			}
		}
	},

	/**
	 * Event dispatched when record preview requested.
	 *
	 * @type {{table: string, sys_id: string}}
	 */
	[ACTIONS.OPEN_RECORD]: {
		schema: {
			type: 'object',
			properties: {
				table: { type: 'string' },
				sys_id: { type: 'string' }
			},
		},
	},
};
