import {createCustomElement} from '@servicenow/ui-core';
import snabbdom from '@servicenow/ui-renderer-snabbdom';

import view from './view';
import properties from './properties';
import actions from './actions';
import actionHandlers from './actionHandlers';
import state from './state';
import styles from './styles.scss';
import behaviors from './beahviors';
import dispatches from './dispatches';


/**
 * Customer Activity allows agents to view and filter the customer activity feed.
 *
 * ```jsx
 * <sn-customer-activity activities={activities} facets={facets}/>
 * ```
 *
 * @seismicElement sn-customer-activity
 * @summary Seismic component that allows agents to view and filter the customer activity feed.
 */
createCustomElement('sn-customer-activity', {
	renderer: {type: snabbdom},
	properties,
	initialState: state,
	view,
	styles,
	behaviors,
	actions,
	actionHandlers,
	dispatches
});
