import {actionTypes} from '@servicenow/ui-core';
import activityFeedActionHandlers from './activity-feed/actionHandlers';
import facetActionHandlers from './facets/actionHandlers';
import {setState} from './utils';
import moment from 'moment-timezone';

import {
	ACTIONS,
	FACET_CONSTANTS,
	FILTER_TYPE,
	INTERNAL_DATE_TIME_FORMAT
} from './constants';

const getProperty = (propName, sysProperties, defaultValue) => {
	if(!sysProperties  ||  !propName)
		return defaultValue;

	if(Object.keys(sysProperties).indexOf(propName) > -1)
		return sysProperties[propName];

	return defaultValue;
};

const _updateDates = (sysProps, updateState, dispatch, runFilters) => {
	const initialOffsetDays = Math.max(getProperty( 'sn_customercentral.data.scope.days_limit', sysProps,30) || 30, 0);

	setState(updateState, 'sysProps.daysLimit', initialOffsetDays);
	setState(updateState, 'filters.lastFeedRefreshedOn', moment.utc().format(INTERNAL_DATE_TIME_FORMAT));

	//update the start and end dates
	setState(updateState, 'filters.startDate',
		moment.utc().subtract(initialOffsetDays, 'days').format(INTERNAL_DATE_TIME_FORMAT));
	setState(updateState, 'filters.endDate', moment.utc().format(INTERNAL_DATE_TIME_FORMAT));

	!runFilters || dispatch(ACTIONS.FILTER_APPLIED, {filterType: FILTER_TYPE.DATE, refreshFacets: true});
};

const onDateRangeChange = ({state: {timeZoneOffset}, action: {payload}, dispatch, updateState}) => {
	const {start, end} = payload;
	//These are user inputs, so we have to bring them back to UTC.
	//Get start and end in UTC and update them to state.
	setState(updateState, 'filters.startDate',
		moment.utc(start).startOf('day').utcOffset(-1 * timeZoneOffset).format(INTERNAL_DATE_TIME_FORMAT));
	setState(updateState, 'filters.endDate',
		moment.utc(end).endOf('day').utcOffset(-1 * timeZoneOffset).format(INTERNAL_DATE_TIME_FORMAT));
	setState(updateState,'filters.windows', {});
	setState(updateState, 'filters.lastFeedRefreshedOn',  moment.utc().format(INTERNAL_DATE_TIME_FORMAT));

	dispatch(ACTIONS.FILTER_APPLIED, {filterType: FILTER_TYPE.DATE, refreshFacets: true});
};

const _updateFeed = (state, newFeed, updateState) => {
	const {feed: {items}, properties:{activitiesListSize}, filters} = state;
	const {feedItems = [], windowFrom, windowTo, hasMoreRecords} = newFeed || {};

	const window = filters.windows[filters.facetKey] || {};
	let sliceTo = Math.min(activitiesListSize, (windowTo - windowFrom));
	if(window && Object.keys(window).length > 0) {
		sliceTo = Math.max(sliceTo, window.windowTo || activitiesListSize);
	}

	let dirtyFeed = {
		items: [...items.slice(0, windowFrom), ...feedItems.slice(0,sliceTo)],
		hasMoreRecords: hasMoreRecords || (sliceTo < feedItems.length)
	};
	dirtyFeed.size =  dirtyFeed.items.length;
	setState(updateState, 'isFeedLoading', false);
	setState(updateState, 'isFeedLoadingMore', false);
	setState(updateState, 'feed', dirtyFeed, true);
	//update filters with latest window size
	setState(updateState, 'filters.windows', {
		...filters.windows,
		[filters.facetKey]: {
			...window,
			windowTo: windowFrom + sliceTo
		}
	});
};

const _updateFacets = (state, newFacets, updateState) => {
	const {facets, filters: {filterType}} = state;
	const {facetItems = [], facetKey, activityCount} = newFacets || {};
	let dirtyFacets = [];
	if (facetKey && facetKey.length > 0 &&
		facetKey != FACET_CONSTANTS.ALL_FACETS_KEY &&
		filterType != FILTER_TYPE.DATE &&
		facets.length > 0) {
		facets.forEach(facet => {
			if (facet.key == facetKey) {
				facet.children = [...facetItems];
				facet.activity_count = activityCount;
			}
			dirtyFacets.push({...facet});
		});
	} else if (!facetKey || facetKey.length == 0 ||
		facetKey == FACET_CONSTANTS.ALL_FACETS_KEY ||
		filterType == FILTER_TYPE.DATE
	) {
		dirtyFacets = [...facetItems];
	}
	setState(updateState, 'isFacetsLoading', false);
	setState(updateState, 'facets', dirtyFacets, true);
};

const _updateTimezone = (newTimeZone, updateState) => {
	setState(updateState, 'timeZoneOffset', moment.utc().tz(newTimeZone || 'GMT')._offset);
};

const _updateSysProps = (newSysProps, updateState, dispatch) => {
	_updateDates(newSysProps, updateState, dispatch, true);
};

const onPropertyChange = ({state, action: {payload: {name, value}}, updateState, dispatch}) => {
	switch (name) {
	case 'feed':
		_updateFeed(state, value, updateState);
		break;
	case 'facets':
		_updateFacets(state, value, updateState);
		break;
	case 'timeZone':
		_updateTimezone(value, updateState);
		break;
	case 'sysProps':
		_updateSysProps(value, updateState, dispatch);
		break;
	}
};

const onFilterApplied = (
	{
		state: {filters},
		properties: {activitiesListSize, loadChildFacetsOnDemand, showCountOnFacet},
		action: {payload},
		dispatch,
		updateState
	}
) => {
	const {filterType, refreshFacets} = payload;
	let newFilters = {...filters};

	const facetWindow = newFilters.windows[newFilters.facetKey] || {};

	setState(updateState, 'filters.filterType', filterType);
	dispatch(ACTIONS.FEED_REQUESTED, {
		...payload,
		windowFrom: facetWindow.windowFrom || 0,
		windowTo: facetWindow.windowTo || activitiesListSize,
		facets: newFilters.facets && newFilters.facets.length > 0 ? newFilters.facets.join() : undefined,
		recordId: newFilters.recordId,
		startDate: newFilters.startDate,
		endDate: newFilters.endDate,
		lastFeedRefreshedOn: newFilters.lastFeedRefreshedOn
	});
	if (refreshFacets) {
		dispatch(ACTIONS.FACETS_REQUESTED, {
			facets: filterType != FILTER_TYPE.DATE ? (newFilters.facets || []).join() : undefined,
			facetKey: newFilters.facetKey,
			showCounts: !loadChildFacetsOnDemand && showCountOnFacet,
			lazyLoad: filterType == FILTER_TYPE.FACET && loadChildFacetsOnDemand &&
				(!newFilters.facets || newFilters.facets.length == 0),
			startDate: newFilters.startDate,
			endDate: newFilters.endDate,
			lastFeedRefreshedOn: newFilters.lastFeedRefreshedOn
		});
	}
};

const onBootstrap = ({properties, updateState, dispatch}) => {
	// update dates
	_updateDates(properties.sysProps, updateState, dispatch);

	//update the timezoneOffset
	_updateTimezone(properties.timeZone, updateState);

	dispatch(ACTIONS.FILTER_APPLIED, {filterType: FILTER_TYPE.DATE, refreshFacets: true, isBootStrap: true});
};

const myHandlers = {
	[actionTypes.COMPONENT_BOOTSTRAPPED]: onBootstrap,
	[actionTypes.COMPONENT_PROPERTY_CHANGED]: onPropertyChange,
	[ACTIONS.DATE_RANGE_PICKER_VALUE_CHANGED]: onDateRangeChange,
	[ACTIONS.FILTER_APPLIED]: onFilterApplied
};

//TODO: Uncomment me for development version
//  import dbAction from '../../example/mockingDataBroker';

const actionHandlers = {
	...facetActionHandlers,
	...activityFeedActionHandlers,
	...myHandlers,
	//TODO: Uncomment me for development version
	//  ...dbAction
};

export default actionHandlers;
