import {ACTIONS, FACET_CONSTANTS, FILTER_TYPE, INTERNAL_DATE_TIME_FORMAT} from '../constants';
import {setState} from '../utils';
import moment from 'moment';

const onFacetGroupSelection =
	({state, action, updateState, dispatch}) => {
		const {selectedFacet, filters, properties: {loadChildFacetsOnDemand}} = state;
		const {payload: {facet}} = action;
		const {name, title, facets = undefined, is_dynamic, key} = facet;

		// Avoid duplicate selections through UI.
		if (selectedFacet == key && key != FACET_CONSTANTS.ALL_FACETS_KEY)
			return;

		setState(updateState, 'selectedFacet', key);
		setState(updateState, 'expandedFacet', key);
		setState(updateState, 'title', title || name);
		setState(updateState, 'filters', {
			...filters,
			facets,
			facetKey: key,
			recordId: undefined, //As it is a group selection
			lastFeedRefreshedOn:
				key != FACET_CONSTANTS.ALL_FACETS_KEY ?
					filters.lastFeedRefreshedOn : moment.utc().format(INTERNAL_DATE_TIME_FORMAT),
			windows: key != FACET_CONSTANTS.ALL_FACETS_KEY ? {
				...filters.windows,
				[key]: {
					...filters.windows[key],
					windowFrom: 0 // Switched to new facet-group, so get the feed from 0 to windowTo.
				}
			} : {} //In case of all activities, reset everything
		});

		dispatch(ACTIONS.FILTER_APPLIED, {
			filterType: FILTER_TYPE.FACET,
			refreshFacets: key == FACET_CONSTANTS.ALL_FACETS_KEY || is_dynamic && loadChildFacetsOnDemand
		});
	};

const onFacetSelection =
	({state: {selectedFacet, filters}, action: {payload}, updateState, dispatch}) => {

		if (selectedFacet == payload.selectedFacet)
			return;

		setState(updateState, 'selectedFacet', payload.selectedFacet);
		setState(updateState, 'title', payload.title);
		setState(updateState, 'filters', {
			...filters,
			facets: [payload.facet],
			recordId: payload.sys_id,
			facetKey: payload.selectedFacet,
			windows: {
				...filters.windows,
				[payload.selectedFacet]: {
					...filters.windows[payload.selectedFacet],
					windowFrom: 0
				}
			}
		});

		dispatch(ACTIONS.FILTER_APPLIED, {
			filterType: FILTER_TYPE.FACET
		});
	};

const onShowMoreRequest = (
	{properties: {childFacetsListSize}, action: {payload}, updateState}) => {
	const {facet} = payload;
	facet.childrenDisplayLimit = (facet.childrenDisplayLimit || childFacetsListSize) + 100000;
	setState(updateState, 'random', Math.random());
};

const actionHandlers = {
	[ACTIONS.FACET_SELECTED]: onFacetSelection,
	[ACTIONS.FACET_GROUP_SELECTED]: onFacetGroupSelection,
	[ACTIONS.FACET_SHOW_MORE_REQUESTED]: onShowMoreRequest
};

export default actionHandlers;
