import {ACTIONS, FILTER_TYPE} from '../constants';
import {setState} from '../utils';

//TODO: fix me (STRY51135676)
const openRecord = ({action: {payload}, dispatch}) => {
	if (payload && payload.table && payload.sys_id) {
		dispatch(ACTIONS.OPEN_RECORD, payload);
	}
};

const fetchMoreActivities = (
	{
		state: {filters},
		properties: {activitiesListSize},
		dispatch,
		updateState
	}) => {
	setState(updateState, 'isFeedLoadingMore', true, true);
	const window = filters.windows[filters.facetKey] || {};
	setState(updateState, 'filters.windows', {
		...filters.windows,
		[filters.facetKey]: {
			...window,
			windowFrom: (window.windowTo || activitiesListSize),
			windowTo: (Math.min(window.windowTo || activitiesListSize)) + activitiesListSize,
		}
	});

	dispatch(ACTIONS.FILTER_APPLIED, {filterType: FILTER_TYPE.SHOW_MORE});
};

const actionHandlers = {
	[ACTIONS.FEED_LINK_CLICKED]: openRecord,
	[ACTIONS.FEED_SHOW_MORE_CLICKED]: fetchMoreActivities
};

export default actionHandlers;
