import '@servicenow/now-card';
import '@servicenow/now-icon';
import '@servicenow/now-button';
import '@servicenow/now-loader';
import '@servicenow/now-empty-state';
import moment from 'moment';
import {ACTIONS, FACET_CONSTANTS} from '../constants';
import {t} from 'sn-translate';
import '@servicenow/now-text-link';

const extractDateFormat = (a) => {
	if (a.match(/[HhkaAmsSZ]/g))
		a = a.replace(/[HhkaAmsSZ]/g, '');
	do {
		a = a.replace(/[: T.-]$/g, '');
	} while (a.match(/[: T.-]$/g));
	return a;
};

const _renderFeedDate = (timelineDay, today, dateFormat) => {
	let dateInText = '';
	let todayCopy = today.clone();
	if (timelineDay.isSame(today, 'day')) {
		dateInText = t('Today');
	} else if (timelineDay.isSame(todayCopy.subtract(1, 'days'), 'day')) {
		dateInText = t('Yesterday');
	} else {
		dateInText = timelineDay.format(extractDateFormat(dateFormat));
	}
	return <div className="feed-card-date">{dateInText}</div>;
};

const _renderSubHeaderFields = (
	subHeaderField,
	properties,
	subobject_table_name,
	subobject_table_label,
	subobject_sys_id,
	timeZoneOffset
) => {
	if (!subHeaderField || !subHeaderField.value || subHeaderField.value == '') {
		return (null);
	}

	let subHeaderHTML = [];
	if (subHeaderField.show_label == 'true' && subHeaderField.label) {
		subHeaderHTML.push(subHeaderField.label + ': ');
	}
	if (subHeaderField.deeplink_to_subobject == 'true') {
		subHeaderHTML.push(
			<now-button-bare
				configAria={{'aria-label': subobject_table_label, 'aria-roledescription':'link'}}
				hide-padding
				label={subHeaderField.value}
				size="sm"
				variant="primary"
				append-to-payload={
					{
						table: subobject_table_name,
						sys_id: subobject_sys_id
					}
				}
			/>
		);
	} else if (subHeaderField.type == 'glide_date_time' ||
		subHeaderField.type == 'glide_date' ||
		subHeaderField.type == 'glide_time') {
		if (subHeaderField.display_as_timeago == 'true')
			subHeaderHTML.push(t(moment.utc(subHeaderField.value).fromNow()));
		else
			subHeaderHTML.push(moment.utc(subHeaderField.value).utcOffset(
				timeZoneOffset).format(properties.dateFormat)
			);
	} else if (subHeaderField.type == 'email') {
		subHeaderHTML.push(
			<now-text-link
				href={'mailto:' + subHeaderField.value}
				label={subHeaderField.value}>
			</now-text-link>
		);
	} else if (subHeaderField.type == 'ph_number') {
		subHeaderHTML.push(
			<now-text-link
				href={'tel:' + subHeaderField.value}
				label={subHeaderField.value}>
			</now-text-link>
		);
	} else
		subHeaderHTML.push(<span>{subHeaderField.value}</span>);
	return <span className="feed-card-subheader-field">{subHeaderHTML}</span>;
};


const _renderFeedItemContent = (feedItem, feedItemIndex, contentFields, feedItemProps, updateState) => {
	if (!contentFields || contentFields.length == 0) {
		return null;
	}
	const feedItemPath = 'feedItemProps.' + feedItem.sys_id;
	let hasContent = false;
	let feedItemContent = <div>
		{
			contentFields.map((contentField) => {
				hasContent = hasContent || contentField.value != '';

				return contentField.type == 'translated_html' ?
					<div innerHTML={contentField.value}></div> :
					contentField.value;
			})
		}
	</div>;

	if (!hasContent) {
		return null;
	}

	let feedBodyHTML = (
		<div
			className={'feed-card-body-main-content' + (!feedItemProps || feedItemProps.showTruncated ? ' truncated' : '')}
			ref={
				element => {
					//since line height is 14px, 2 lines height will be 28px and giving 2px for gap between lines
					if (element.scrollHeight > 30) {
						if (!feedItemProps || !feedItemProps.isLarge)
							updateState({
								path: feedItemPath,
								value: {'isLarge': true, 'showTruncated': true},
								operation: 'set'
							});
					} else {
						if (!feedItemProps || feedItemProps.isLarge)
							updateState({
								path: feedItemPath,
								value: {'isLarge': false},
								operation: 'set'
							});
					}
				}
			}
		>
			{feedItemContent}
			<span>
			</span>
		</div>
	);

	if (feedItemProps) {
		if (feedItemProps.isLarge) {
			let labelValue = t('See less');
			if (feedItemProps.showTruncated) {
				labelValue = t('See more');
			}
			feedBodyHTML = [feedBodyHTML];
			feedBodyHTML.push(
				<now-button-bare
					configAria={{'aria-roledescription':'link'}}
					hide-padding
					label={labelValue}
					on-click={() => {
						updateState({
							path: feedItemPath + '.showTruncated',
							value: (!feedItemProps.showTruncated),
							operation: 'set'
						});
					}}
					size="sm"
					variant="primary">
				</now-button-bare>);
		}
	}
	return feedBodyHTML;
};

const _renderFeedItem = (feedItem, feedItemIndex, {properties, feedItemProps, timeZoneOffset}, {updateState}) => {
	const {
		icon, title, subheader_fields, content_fields,
		subobject_table_name, subobject_table_label, subobject_sys_id
	} = feedItem;
	const {hideFeedIcons} = properties;
	const noFeedIcon = hideFeedIcons || (!icon);
	return (
		<now-card
			className="feed-card"
			interaction="none"
			size="lg">
			<div className="feed-card-header">
				{!noFeedIcon ?
					<now-icon icon={icon} size="md" className="feed-card-icon"></now-icon> :
					<div className="feed-card-no-icon"></div>
				}
				<span>{title}</span>
			</div>
			<div className={noFeedIcon ? 'feed-card-body' : 'feed-card-body has-feed-icon'}>
				<div className="feed-card-subheader">
					{
						subheader_fields.map((subHeaderField) =>
							_renderSubHeaderFields(
								subHeaderField,
								properties,
								subobject_table_name,
								subobject_table_label,
								subobject_sys_id,
								timeZoneOffset
							)
						)
					}
				</div>
				{
					(content_fields &&
						content_fields.length != 0) ?
						_renderFeedItemContent(
							feedItem,
							feedItemIndex,
							content_fields,
							feedItemProps[feedItem.sys_id],
							updateState
						) :
						null
				}
			</div>
		</now-card>);
};

const _renderEmptyFeed = (selectedFacet) => {
	let headerText = t('No activity found for any facets.');
	let bodyText = t('Try changing the date range.');
	if (selectedFacet != FACET_CONSTANTS.ALL_ACTIVITY_HEAD) {
		headerText = t('No activity found for this facet.');
		bodyText = t('Try changing the date range, or selecting a different facet.');
	}
	return <now-empty-state
		type="empty"
		variant="fit"
		icon={null}
		headerText={headerText}
		bodyText={bodyText}>
	</now-empty-state>;
};

const _renderFeedLoading = (isShowMore) => {
	return <now-loader
		className={isShowMore ? 'feed-showmore-loader' : 'feed-loader'}
		size="lg"
	/>;
};

const _renderVerticalLine = (hideTimeLeaps) => {
	return hideTimeLeaps ? <div className="feed-card-vertical-separator"></div> :
		<div className="feed-card-vertical-line-separator"></div>;
};

const view = (state, helpers) => {
	const {
		properties,
		isFeedLoading,
		isFeedLoadingMore,
		selectedFacet,
		feed,
		timeZoneOffset = 0
	} = state;

	const {dateFormat, hideTimeLeaps, feed: feedProperty} = properties;

	if (isFeedLoading && (!feed || !feed.items || feed.items.length == 0)
		&& feedProperty && Object.keys(feedProperty).length > 0)
		return _renderFeedLoading();

	if (!feed)
		return _renderEmptyFeed(selectedFacet);

	const {items: feedItems, hasMoreRecords} = feed;

	if (!feedItems || feedItems.length == 0)
		return _renderEmptyFeed(selectedFacet);

	const today = moment.utc().utcOffset(timeZoneOffset);
	let feedHTML = [];

	let prevFeedItem;
	let timelineDate;
	feedItems.map((feedItem, feedItemIndex) => {
		const feedItemPostedOn = moment.utc(feedItem.created).utcOffset(timeZoneOffset);
		if (!prevFeedItem || prevFeedItem && timelineDate && !timelineDate.isSame(feedItemPostedOn, 'day')) {
			feedHTML.pop(); //Remove the last vertical line for the previous day.
			feedHTML.push(_renderFeedDate(feedItemPostedOn, today, dateFormat));
		}

		feedHTML.push(_renderFeedItem(feedItem, feedItemIndex, state, helpers));
		feedHTML.push(_renderVerticalLine(hideTimeLeaps));
		timelineDate = feedItemPostedOn;
		prevFeedItem = feedItem;
	});

	feedHTML.pop(); //remove the last vertical line

	if (hasMoreRecords) {
		if (!isFeedLoadingMore)
			feedHTML.push(
				<now-button
					label={t('Show more')}
					className="show-more-button"
					variant="primary"
					on-click={() => {
						let payLoad = {
							'stFrom': feedItems.length - 1
						};
						helpers.dispatch(ACTIONS.FEED_SHOW_MORE_CLICKED, payLoad);
					}}>
				</now-button>);
		else
			feedHTML.push(_renderFeedLoading(true));
	}
	return <div className="feed-card-item-container">
		{feedHTML}
	</div>;
};

export default view;
