import '@servicenow/now-date-range-picker';
import '@servicenow/now-heading';
import renderFacets from './facets/view';
import renderFeed from './activity-feed/view';
import {INTERNAL_DATE_FORMAT} from './constants';
import {t} from 'sn-translate';
import moment from 'moment';


const _renderDateRangePicker = (startDate, endDate, timeZoneOffset) => {
	const start = moment.utc(startDate).utcOffset(timeZoneOffset).format(INTERNAL_DATE_FORMAT);
	const end = moment.utc(endDate).utcOffset(timeZoneOffset).format(INTERNAL_DATE_FORMAT);

	return <now-date-range-picker
		className="small-margin date-picker-container"
		start={start}
		end={end}
		format={INTERNAL_DATE_FORMAT}
	/>;
};

const _renderFeedHeader = (label = t('All activity')) => {
	return <now-heading
		className="feed-container-title"
		label={label}
		level="4"
		variant="header-tertiary"
		hasNoMargin={true}
		wontWrap
	/>;
};

const _renderFeedFilters = (state) => {
	const {properties: {disableDateRangeFilter}, timeZoneOffset, filters: {startDate, endDate}} = state;
	return !disableDateRangeFilter ? _renderDateRangePicker(startDate, endDate, timeZoneOffset) : null;
};

const _renderFacets = (state, helpers) => {
	return <div className="facet-container light-border">
		{renderFacets(state, helpers)}
	</div>;
};

const _renderFeed = (state, helpers) => {
	return renderFeed(state, helpers);
};

const view = (state, helpers) => {
	const {title, properties: {disableFacets}} = state;
	return <div className="component-container">
		{!disableFacets ? _renderFacets(state, helpers) : ''}
		<div className={'feed-container ' + (disableFacets ? '' : 'facet-separator ') + 'light-border'}>
			<div className="feed-container-header">
				<div className="feed-container-header-title">
					{_renderFeedHeader(title || undefined)}
				</div>
				<div className="feed-container-header-filters">
					{_renderFeedFilters(state)}
				</div>
			</div>
			<hr className="feed-header-hr no-margin"/>
			<div className="feed-container-main-content">
				{_renderFeed(state, helpers)}
			</div>
		</div>
	</div>;

};

export default view;
